function findLinkEntities(contentBlock, callback, contentState) {
  contentBlock.findEntityRanges(
      (character) => {
        const entityKey = character.getEntity();
        return (
            entityKey !== null &&
            contentState.getEntity(entityKey).getType() === 'LINK'
        );
      },
      callback
  );
}

const Link = (props) => {
  const {url} = props.contentState.getEntity(props.entityKey).getData();
  return (
      <a href={url} style={styles.link}>
        {props.children}
      </a>
  );
};

function findImageEntities(contentBlock, callback, contentState) {
  contentBlock.findEntityRanges(
      (character) => {
        const entityKey = character.getEntity();
        return (
            entityKey !== null &&
            contentState.getEntity(entityKey).getType() === 'IMAGE'
        );
      },
      callback
  );
}

const Image = (props) => {
  const {
    height,
    src,
    width,
  } = props.contentState.getEntity(props.entityKey).getData();

  return (
      <img src={src} height={height} width={width} />
  );
};

const styles = {
  root: {
    fontFamily: '\'Helvetica\', sans-serif',
    padding: 20,
    width: 600,
  },
  editor: {
    border: '1px solid #ccc',
    cursor: 'text',
    minHeight: 80,
    padding: 10,
  },
  button: {
    marginTop: 10,
    textAlign: 'center',
  },
};


class App extends React.Component {

  constructor(props) {
    super(props);

    const decorator = new CompositeDecorator([
      {
        strategy: findLinkEntities,
        component: Link,
      },
      {
        strategy: findImageEntities,
        component: Image,
      },
    ]);

    const blocksFromHTML = convertFromHTML(initMarkup);
    const state = ContentState.createFromBlockArray(
        blocksFromHTML.contentBlocks,
        blocksFromHTML.entityMap,
    );

    this.state = {
      draftEditorState: DraftEditorState.createWithContent(
          state,
          decorator,
      ),
      reactRTEState: RichTextEditor.createValueFromString(initMarkup,'html'),
      value: initMarkup
    };
    this.onChange = editorState => {
       console.log('Draft Change', editorState)
      this.setState({editorState})
    };
  }

  handleEditorChange = (content, editor) => {
    console.log('Content was updated:', content);
  };

  onRTEChange = (value) => {
    this.setState({reactRTEState: value});
    console.log('RTE Change', value)
    if (this.props.onChange) {
      // Send the changes up to the parent component as an HTML string.
      // This is here to demonstrate using `.toString()` but in a real app it
      // would be better to avoid generating a string on each change.
      this.props.onChange(
          value.toString('html')
      );
    }
  };

  render() {
    return (
        <>
          <div>
            <h1>Raw Textarea</h1>
            <textarea
               value={this.state.value}
               onChange={(event) => {
                 console.log('textarea change',event.target.value )
                 this.setState({value: event.target.value})}
               }
            />
          </div>

          <div>
            <h1>Tiny MCE</h1>
            <TinyMCEEditor
                initialValue={this.state.value}
                init={{
                  height: 500,
                  menubar: false,
                  plugins: [
                    'advlist autolink lists link image charmap print preview anchor',
                    'searchreplace visualblocks code fullscreen',
                    'insertdatetime media table paste code help wordcount',
                  ],
                  toolbar:
                      'undo redo | formatselect | bold italic backcolor | alignleft aligncenter alignright alignjustify |   bullist numlist outdent indent | removeformat | help',
                }}
                onEditorChange={this.handleEditorChange}
            />
          </div>

          <div>
            <h1>CK Editor</h1>
            <CKEditor
                editor={ ClassicEditor }
                data={this.state.value}
                onInit={ editor => {
                  // You can store the "editor" and use when it is needed.
                  console.log( 'Editor is ready to use!', editor );
                } }
                onChange={ ( event, editor ) => {
                  const data = editor.getData();
                  console.log( { event, editor, data } );
                } }
                onBlur={ ( event, editor ) => {
                  console.log( 'Blur.', editor );
                } }
                onFocus={ ( event, editor ) => {
                  console.log( 'Focus.', editor );
                } }
            />
          </div>


          <div>
            <h1>React RTE</h1>
            <RichTextEditor
                value={this.state.reactRTEState}
                onChange={this.onRTEChange}
                toolbarConfig={toolbarConfig}
            />

          </div>

          <div>
            <h1>DraftJS  - core</h1>
            <DraftEditor
                editorState={this.state.draftEditorState}
                onChange={this.onChange}
            />
          </div>


          <div>
            <h1>DraftJS WSIWYG</h1>
            <WSIWYGEEditor
                editorState={this.state.draftEditorState}
                // toolbarClassName="toolbarClassName"
                // wrapperClassName="wrapperClassName"
                // editorClassName="editorClassName"
                onEditorStateChange={this.onChange}
            />
          </div>

        </>
    );
  }
}

export default App;
